.antv_container {
  width: 632px;
  margin: 30px auto;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.antv_container h3 {
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
}
.antv_container .header {
  padding-bottom: 8px;
}
.antv_container .header button {
  margin-right: 8px;
}
