.dataset_container .dataset_container_main {
  background: #fff;
  border-radius: 5px;
  width: 960px;
  padding: 10px 20px 20px 20px;
  box-sizing: border-box;
  margin: 0 auto;
  font-size: 12px;
}
.dataset_container .dataset_container_main .dataset_container_content {
  border: 1px solid #eee;
  min-height: 300px;
  margin-top: 10px;
}
.dataset_container .dataset_container_main .dataset_container_content .left {
  width: 700px;
  border-right: 1px solid #eee;
}
.dataset_container .dataset_container_main .dataset_container_content .left .label-warp {
  height: 460px;
  position: relative;
}
.dataset_container .dataset_container_main h1 {
  text-align: center;
  padding: 8px 0;
  font-size: 16px;
}
.dataset_container .dataset_container_main .dataset_type_header {
  height: 46px;
  line-height: 46px;
  padding: 0 20px;
  color: #666;
  background-color: #f7f7f7;
  border-bottom: 1px solid #eee;
}
.dataset_container .dataset_container_main .dataset_type_header .anticon {
  margin-right: 15px;
  font-size: 14px;
  cursor: pointer;
}
.dataset_container .dataset_container_main .dataset_type_header .anticon.active {
  color: #40a9ff;
}
.container {
  width: 100%;
  height: 100vh;
  background: #f4f6f7;
  padding: 20px;
}
.box_flex {
  display: flex;
}
.flex {
  flex: 1 1;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
.clearfix:after {
  display: block;
  content: "";
  height: 0;
  visibility: hidden;
  clear: both;
}
.none {
  display: none;
}
